html, body {
  overflow: hidden;
}

.sortableHelper {
  z-index: 4000; /* required so that react-sortable-hoc items are on top while dragging */
  background-color: transparent;
}

.App {
  background-color: red;
}

.drawer {
  width: 22.5vw;
  height: 100vh
}

@media (max-width: 600px) {
  .drawer {
      width: 100vw;
  }
}



